:root {
  --bg-color: aliceblue;
  --bg-lighter-color: azure;
  --bg-darker-color: lightsteelblue;
  --fg-color: #333333;
  --fg-lighter-color: #444444;
  --info-color: #aaaaaa;
  --hidden-color: #bbbbbb;
  --input-bg-color: #fcfcfc;
  --btn-bg-color: #eeeeee;
  --btn-hover-bg-color: #dddddd;
  --btn-fg-color: var(--fg-color);
}

html {
  overflow-y: scroll;
}

.reverse-color {
  background: var(--fg-color);
  color: var(--bg-color);
}

.reverse-color a {
  text-decoration: none;
  color: var(--bg-color);
}

html,body {
  margin: 0;
  background: var(--bg-color);
  color: var(--fg-color);
  font-family: 'Arial';
  height: 100%;
}

main {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  width: 100%;
}

.center {
  margin: 2em auto;
}

.clickable {
  cursor: pointer;
}

.center-children {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.narrow {
  max-width: 40%;
}

.wide {
  width: 60%;
}

#root-page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  min-height: 100vh;
}

button {
  min-width: 120px;
  height: 32px;
  line-height: 32px;
  background: white;
  color: #333;
  border: none;
  -webkit-box-shadow: 0 1px 3px #aaa;
          box-shadow: 0 1px 3px #aaa;
}

button.primary {
  background: steelblue;
  color: white;
}

button:hover {
  cursor: pointer;
}

input[type=text], input[type=password] {
  border: none;
  min-width: 16em;
  height: 32px;
  -webkit-box-shadow: inset 0 0 2px #aaa;
          box-shadow: inset 0 0 2px #aaa;
  padding: 0 .2em;
}

/* ------------------------------------------ header & footer */

header, footer {
  -webkit-box-shadow: 0 0 5px var(--fg-color);
          box-shadow: 0 0 5px var(--fg-color);
}

header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

header > *:last-child {
  margin-left: auto;
  margin-right: 2em;
  text-align: center;
}

header .username {
  text-align: right;
}

footer {
  text-align: center;
  line-height: 3;
}

nav {
  line-height: 3;
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0 2em;
}

nav ul li {
  display: inline-block;
  margin: 0 1em;
}

/* ------------------------------------------ login */

#login-page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  margin-top: 5%;
}

form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

form > * {
  margin: .6em 0;
}

#login-page input {
  padding: 0 .5em;
}

/* ------------------------------------------ blogs */

.blogs {
  width: 45%;
  margin: 3em auto;
  font-size: .9em;
}

.blog {
  background: #fefefe;
  padding: 2em 3em;
  -webkit-box-shadow: 0 0 5px #ddd;
          box-shadow: 0 0 5px #ddd;
  margin: 1em 0;
}

.blog {
  line-height: 2;
}

.blog pre, .blog code {
  font-family: "Consolas", "Courier New", "monospace";
  font-size: .9em;
}

.blog pre {
  background: #e5f3ff;
  padding: 1em;
}

.blog pre code {
  background: #e5f3ff;
}

.blog code {
  background: #cce7ff;
  padding: 0 .3em;
  border-radius: 1px;
}

.blog-content img {
  max-width: 1366px;
}

.blog-content a {
  border-bottom: 1px dashed var(--fg-color);
  word-wrap: break-word;
}

.blog .footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: baseline;
      align-items: baseline;
}

/*
.blog .footer .comments .number {
  display: inline-block;
  width: 1em;
  height: 1em;
}
*/

.blog .footer .right {
  margin-left: auto;
}

.blog .footer * {
  display: inline;
}

.blog blockquote {
  border-left: 5px solid #ddd;
  margin-left: 0;
  padding-left: 2em;
}

.blog p {
  margin: .2em 0;
}

.blog .footer {
  margin-top: 1em;
}

.blog br {
  margin: 0;
}

.blog-content a {
  color: steelblue;
  border: none;
}

.blog-content a:hover {
  text-decoration: underline;
}

.blog .video embed {
  padding: 1em;
  -webkit-box-shadow: 0 0 10px #aaa;
          box-shadow: 0 0 10px #aaa;
  margin: 0 auto;
}

.info {
  -webkit-filter: opacity(.3);
          filter: opacity(.3);
}

.blog .footer {
  font-size: .9em;
}

.blog .footer .info:hover {
  -webkit-filter: opacity(.6);
          filter: opacity(.6);
}

.blog .tags {
  margin-right: 4em;
}

.blog .tag {
  margin: 0 .4em;
}

.blog .tag::before {
  content: '# ';
}

.comments-content {
  margin: 1em;
}

.comment {
  padding: .8em 1.2em;
  background: white;
  -webkit-box-shadow: 0 0 3px #aaa;
          box-shadow: 0 0 3px #aaa;
  margin-bottom: .8em;
}

.datetime {
  text-align: right;
}

.panel {
  position: fixed;
  right: 5%;
  bottom: 10%;
}

.edit-blog textarea {
  width: 100%;
  min-height: 40em;
  line-height: 1.5;
}

.panel {
  -webkit-filter: opacity(.1);
          filter: opacity(.1);
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.panel:hover {
  -webkit-filter: opacity(1.0);
          filter: opacity(1.0);
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

/* ------------------------------------------ */

a {
  color: var(--fg-color);
  text-decoration: none;
}

.content a[!icon] {
  border-bottom: 1px solid var(--info-color);
  padding-bottom: 2px;
}

.content a:hover {
  color: var(--fg-color);
}

.about img {
  -webkit-box-shadow: 0 0 10px #aaa;
          box-shadow: 0 0 10px #aaa;
}

.about hr {
  margin: 2em 0;
}

.about .age {
  display: table;
  margin: 0 auto;
}

.about .age p {
  display: table-row;
}

.about .age span {
  display: table-cell;
  height: 1.4em;
}

.about .age p {
  margin: .3em 0;
}

.about .age span.unit {
  font-size: 0.6em;
  -webkit-filter: opacity(50%);
          filter: opacity(50%);
}

.about .num {
  display: inline-block;
  min-width: 2em;
}

.about .links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 1em;
}

.about .links > *:first-child {
  margin-right: 1em;
}

.about .links > * {
  line-height: 20px;
}

.about .hr {
  width: 80%;
  border-bottom: 1px solid rgba(0,0,0,.2);
  margin: 1em auto 2em auto;
}

.sticky {
  position: fixed;
  right: 5%;
  bottom: 10%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  z-index: 999;
}

.sticky > * {
  -webkit-filter: opacity(20%);
          filter: opacity(20%);
}

.sticky > *:hover {
  -webkit-filter: opacity(100%);
          filter: opacity(100%);
}

.tooltip-container {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: flex-center;
      align-items: flex-center;
}

.tooltip-container .tooltip {
  position: absolute;
  visibility: hidden;
  min-width: 8em;
  text-align: center;
  right: 30px;
  -ms-flex-order: -1;
      order: -1;
  opacity: 0;
  border-radius: 2px;
}

/* arrow */
.tooltip-container .tooltip::after {
  content: " ";
  position: absolute;
  top: 50%;
  left: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent var(--fg-color);
  z-order: 1
}

.tooltip-container:hover .tooltip {
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
}

/* ------------------------------------------ gallery */

.gallery {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 2em 0;
}

.gallery img {
  display: block;
  max-width: 300px;
  width: auto;
  height: auto;
  margin: 1em;
  -webkit-box-shadow: 0px 0px 10px #aaa;
          box-shadow: 0px 0px 10px #aaa;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.gallery img:hover {
  -webkit-box-shadow: 0px 0px 30px #aaa;
          box-shadow: 0px 0px 30px #aaa;
  cursor: pointer;
  -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
          transform: scale(1.02);
  -webkit-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
}

/* ------------------------------------------ files */

#files-page {
  margin-top: 1em;
}

.file-explorer {
  width: 50%;
  height: 30em;
  margin: 2em auto;
  padding: 1em 2em;
  font-family: 'Consolas';
  font-size: .9em;
  line-height: 1.5;
  -webkit-box-shadow: inset 0 0 5px #aaa;
          box-shadow: inset 0 0 5px #aaa;
  overflow-y: scroll;
  background: white;
}

.file-list li * {
}

.file-list {
  list-style: none;
  padding: 0;
}

.file-list li {
  padding: .2em 1em;
}

.file-list li:hover {
  cursor: pointer;
  -webkit-box-shadow: 0 0 1px #aaa;
          box-shadow: 0 0 1px #aaa;
}

.file-list li.dir * {
  font-weight: bold;
}

#pagination {
  text-align: center;
  margin: 2em 0;
}

#pagination > * {
  display: inline-block;
  font-size: .8em;
  vertical-align: middle;
}

#pagination #current-page {
  width: 2em;
  min-width: 2em;
  height: 1.5em;
  text-align: center;
}



